import React, { useState } from "react";
import { Form, Col, Row, Button } from "react-bootstrap";
import Narbar from "../../components/nabar";
import "./styles.scss";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import iconHome from "../../assets/imgs/iconHome.png";
const fakeAccount = { username: "admin", password: "admin" };

export default function Login() {
  const navigate = useNavigate();
  const [username, setUsername] = useState();
  const [password, setPassword] = useState();

  const handleChane = (val, field) => {
    if (field === "username") {
      setUsername(val);
      console.log("USERNAME: ", val);
    } else {
      setPassword(val);
      console.log("PASSWORD: ", val);
    }
  };

  const handleLogin = () => {
    const url = "https://lap-center.herokuapp.com/api/login";
    axios
      .post(url, {
        username: username,
        password: password,
      })
      .then(function (response) {
        console.log("SUCCESS: ", response.data);
        //localStorage gm 4 functions chnh:
        // - setItem => dng  t tn bin v gi tr
        // - getItem => dng  ly gi tr cn ly
        // - removeItem => xa tn bin v gi tr ca bin  ra khi store
        // - clear => xa tt c cc bin v gi tr ca n khi store
        localStorage.setItem("customerName", response.data.userName);
        localStorage.setItem("accessToken", response.data.token);
        localStorage.setItem("userId", response.data.userId);
        localStorage.setItem("isAdmin", response.data?.isAdmin);
        navigate("/");
      })
      .catch(function (error) {
        console.log("ERROR: ", error);
        alert("tn ti khon hoc mt khu khng ng vui lng th li!!!");
      });
  };

  return (
    <div className="logincontainer">
      <img
        src={iconHome}
        alt=""
        width={45}
        height={45}
        className="iconHome"
        title="Trang ch"
        onClick={() => navigate("/")}
      />
      <div className="formlogin">
        <h2>ng Nhp</h2>
        <Form>
          <Form.Group
            as={Row}
            className="mb-3 d-flex justify-content-between"
            controlId="formPlaintextEmail"
          >
            <Col sm="12">
              <Form.Label>Email hoc s in thoi</Form.Label>
              <Form.Control
                type="text"
                placeholder="Email hoc s in thoi"
                value={username}
                onChange={(e) => handleChane(e.target.value, "username")}
              />
            </Col>
          </Form.Group>
          <Form.Group
            as={Row}
            className="mb-3 d-flex justify-content-between"
            controlId="formPlaintextPassword"
          >
            <Col sm="12">
              <Form.Label>Mt khu</Form.Label>
              <Form.Control
                type="password"
                placeholder="Mt khu"
                value={password}
                onChange={(e) => handleChane(e.target.value, "password")}
              />
            </Col>
          </Form.Group>
          <div className="d-flex justify-content-center mt-4">
            <Button variant="success" onClick={handleLogin}>
              ng Nhp
            </Button>{" "}
          </div>
          <div className="dk">
            <a onClick={() => navigate("/register")}>
              {" "}
              <i>To ti khon mi</i>
            </a>
          </div>
        </Form>
      </div>
    </div>
  );
}
